@import "reset"; //Импорт normalize.css

$text:'Open Sans', sans-serif;
$accent-color:#65e496;
$gray:#f5f6fa;
$black:#44444f;

body{
    font-family: 'Roboto Slab', serif;
    line-height: 24px;
}

h1,h2,h3,h4{
    padding: 0;
    margin: 0;
}

.page-main{
    background:url("../img/bg.jpg") no-repeat;
    background-size: cover;
    color: #fff;
    padding-bottom: 162px;
}

p{
    font-family: $text;
    letter-spacing: 0.8px;
    font-size: 15px;
}
h3{
    font-size: 24px;
    letter-spacing: 1.1px;
}

.container{
   
    max-width: 1170px;
    margin: 0 auto;
    
}

.page{
    padding-top: 161px;
    &_gray{
        background-color: #ffffff;

    }
    &__title{
        line-height: 1.2;
        font-size: 48px;
        text-align: center;
        margin-top: 0px;
    letter-spacing: 1.6px;
    }
    &__subtitle{
        color: $accent-color;
        text-align: center;
        letter-spacing: 2.4px;
        margin-top: 6px;
    }
 
}

.header{
    width:100%;
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 60px;
    

@media(max-width:1280px){
    flex-direction: column;
    padding-left:0px;
    padding-right: 0px;
}

    &__logo{
        display: block;
        background: url("../img/Logo.svg") no-repeat;
        width: 138px;
        height: 28px;
    }
   
    &-list{
        display: flex;
        justify-content: flex-end;
        list-style-type: none;

        &__item{
            margin-left: 20px;
            letter-spacing: 0.5px;
            padding-bottom: 5px;
            border-bottom: 3px solid transparent;
            transition: 0.1s ease-in-out;
            &___head-link {
                text-decoration: none;
                font-size: 16px;
                color: #fff;
            }
            &:hover{
                border-bottom: 3px solid $accent-color;
                transition: 0.1s ease-in-out;
            }
        }
        &__item.active{
            border-bottom: 3px solid $accent-color;
        }
    
    }
}
.block-book{
    display: flex;
    justify-content: space-between;
    padding-top: 162px;
    @media (max-width:1280px) {
        flex-direction: column;
        padding-top: 33px;
    }
    &__demo{
        background: url("../img/book.svg") no-repeat;
        width: 447px;
        flex-basis:447px;
        height: 638px;
        display: block; 
        position: relative;
        @media (max-width:1280px) {
           margin-bottom: 50px;
           flex-basis:750px;
           margin: 0 auto;
           width: 418px;
        }
       
        &__title{
            line-height: 1.2;
            font-weight: bold;
            font-size: 36px;
            position: absolute;
            text-transform: uppercase;
            top: 120px;
            letter-spacing: 1px;
            left: 134px;
            span{
                font-size: 14px;
                color: $accent-color;
                font-family: $text;
                display: block;
                letter-spacing: 0.6px;
                padding-top: 9px;
                text-transform: none;
                line-height: 1.3;
            }
        }
        &__author{
            position: absolute;
            bottom: 35px;
            left: 86px;
            font-size: 14px;
            color: $accent-color;
            font-family: $text;
            @media (max-width:1280px) {
                bottom: 150px;
             }
        }
    }
}



.block-text{
    
   flex-basis: calc(93% - 419px);
    @media (max-width:1280px) {
        text-align: center;
        max-width: 700px;
        margin: 0 auto;
        
    }
    &__name{
        font-size: 18px;
        margin-bottom: 26px;
        margin-top: 0;
        letter-spacing: 0.7px;
     
        &__second{
            font-size: 18px;
        margin-bottom: 30px;
        margin-top: -2px;
        letter-spacing: 1.1px;
        
        }

    }
    
       

    &__title{
        line-height: 1.2;
        font-size: 48px;
        font-weight: 700;
        letter-spacing: 1.8px;
        margin-bottom: 38px;
        
        span{
            display: block;
            font-size: 18px;
            color: $accent-color;
            letter-spacing: 0.9px;
            line-height: 1.3;
            margin-top: 8px;
        }

        p{
            font-size: 15px;
        }
    }

    &__text{
        margin-bottom: 18px;
        line-height: 1.7;
        max-width: 598px;
    }

    &__btn{
        font-size: 18px;
        text-decoration: none;
        display: block;
        text-align: center;
        max-width: 250px;
        padding: 14px 0px;
        letter-spacing: 0.7px;
        color:#fff;
        background:  $accent-color;
        border-radius: 3px;
        border-bottom:2px solid #59ba88;
       
      
        margin-top: 43px;

        @media (max-width:1280px) {
           margin: 0 auto;
         }
    }
    small{
        display: block;
        margin-top: 15px;
        font-size: 12px;
        font-style: italic;
    }
}



.block-autor{
    display: flex;
    justify-content: space-between;
    padding-top: 158px;
    
   .block-text{

    &__name{
        margin-bottom: 18px;
        margin-top: 17px;
    }
    &__btn{
        max-width: 238px;
    }
    &--width{
        flex-basis: calc(93% - 521px);
    }
    &__item{
        margin-bottom: 50px;
        &__decription{
            color: #9191a2;
            margin-bottom: 20px;
        }
        &__date{
            font-size: 15px;
            color: #9191a2;
        }
       &__title{
        line-height: 1.2;
        letter-spacing: 0.9px;
           font-size: 18px;
           font-weight: bold;
       }
       &__btn{
           max-width: 136px;
           text-decoration: none;
           background: $accent-color;
           color: #fff;
           padding: 12px 0;
           text-align: center;
            display: block;
            letter-spacing: 1.9px;
            border-radius: 3px;
            @media (max-width:1280px) {
                margin: 0 auto;
              }
       } 
    }
    
}

    .block-autor_woman{
      
        position: relative;
       &__womanimg{
            display: block;
            background: url("../img/womgrad.png") no-repeat;
              
            z-index: 500;
            margin-top: 48px;
            

            @media (max-width:1218px) {
                width: 245px;
                 height: 331px;
                
                 
            }

        }

      

        &__svg{
            position: absolute;
            right: 0;
            z-index: -1;
            @media (max-width:1218px) {
                width: 269px;
                height: 418px;
            }
        }
        &__womanimg--right{
            width: 245px;
            height: 331px;
            margin-top: 48px;
            margin-right: 70px;
        }
        &__womanimg--left{
            margin-left: 50px;
            @media (max-width:1218px) {
               
            }
        }

       &__svg--left{
           left: 0;
       }

    }
}

.numbers{
  
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 199px 0 142px 0;
        @media (max-width:750px){
            flex-direction: column;
        }
    .numbers_list{
        flex-basis: 22%;
        text-align: center;
        font-size: 18px;
        letter-spacing: 0.6px;
        span{
            padding-bottom: 45px;
            display: block;
            font-size: 80px;
            font-weight: 100;
            font-family: 'Roboto Slab', serif;
            color: $accent-color;
            
        }
    }
}

.page_grays{
    background: $gray;
}


.page_gray{
    background: $gray;
    padding-bottom: 30px;
    padding-top: 149px;
    
     @media (max-width:1280px) {
        padding-bottom: 68px;
     }
}
.block_about{
    display: flex;
    justify-content: space-between;
    margin-top: 85px;
    margin-bottom: 226px;
    @media (max-width:1280px){
        flex-direction: column;
    }
    &__text{
        flex-basis: 41%;
        @media (max-width:1280px){
            margin: 0 auto;
            max-width: 566px;
            margin-bottom: 50px;
        }
        h3{
            letter-spacing: 1.4px;
            margin-bottom: 18px;
        }
       
        &__deck{
            display: flex;
            align-items: center;
            
            p{
                letter-spacing: 1.1px;
                font-weight: bold;
                font-size: 18px;
                margin-left: 2.2px;
                margin-top: 11px;
            }

            &__buttons{
                list-style-type: none;
                padding-left:19px;
                li{
                    display: inline-block;
                    font-size: 18px;
                  margin-right: 12px;
                }
                a{
                    
                    text-decoration: none;
                    color: $accent-color;
                    vertical-align: middle;
                }
            }
        }
    }
    &__table{
        flex-basis: 49%;
        @media (max-width:1280px){
          margin:0 auto;
        }
        h3{
            margin-bottom: 26px;
            
        }
        &__list{
            padding: 0;
            margin: 0;
            counter-reset: list;
            column-count: 2;
            &>li{
                position: relative;
                padding-left: 36px;
                counter-increment: list;
                list-style-type: none;
                margin-bottom: 8px;
                //letter-spacing: 0.7px;
                &::before{
                    content: counter(list)". ";
                }
                &::after{
                    content: "+";
                    position: absolute;
                    left: 0;
                    top: 1px;
                    background: $accent-color;
                    color: #fff;
                    padding: 0px 7px;
                    font-size: 15px;
                    font-family: $text;
                }
            }
        }
        button{
            margin-top: 30px;
            margin-left: 5px;
            cursor: pointer;
            outline: none;
            background: $accent-color;
            color: #fff;
            font-size: 18px;
            border: 1px solid $accent-color;
            width: 100%;
            max-width: 135px;
            height: 50px;
            border-radius: 3px;
            border-bottom:2px solid #adedbb;
            
        }
    }
}

.page_other{ 
    padding-bottom: 161px;
    padding-top: 146px;
    &__title{
        line-height: 1.2;
      
        font-size: 48px;
        text-align: center;
        
    letter-spacing: 1.6px;
    }
    &__subtittle{
        color: $accent-color;
        text-align: center;
        letter-spacing: 2.4px;
        margin-top: 12px;
    line-height: 1.3;
    }
&__block{
    display: flex;
  align-items: center;

    justify-content: space-around;
    margin-top: 85px;
    @media (max-width:730px){
        flex-direction: column;
      }
    &__books{
        position: relative;
        text-align: center;
        flex-basis: 29%;
      
        &--names{
            background: url("../img/book.png") no-repeat center;
            display: block;
         
            margin-bottom: 35px;
            height: 410px;
            text-align: center;

        }
        h3{
            margin-bottom: 20px;
        }
        p{
            color: #838395;
            margin-bottom: 13px;
            @media (max-width:730px){
                margin:0 auto;
                max-width: 500px;
              }
        }
        a{
            text-decoration: none;
            color: $accent-color;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 15px;
            }
        }
    }
    &__button{
       
        background: $accent-color;
        font-size: 18px;
        text-decoration: none;
        text-align: center;
        color: #fff;
        display: block;
        max-width: 220px;
      padding: 12px 0;
      letter-spacing: 0.7px;
      margin: 0 auto;
     margin-top: 87px;
    }
}


.page-latest{
  
    margin-top: 120px;
    padding-top: 147px;
    padding-bottom: 160px;
    &__title{
        
        font-size: 48px;
        text-align: center;
        letter-spacing: 2.2px;
        line-height: 1.2;
    }
    &__subtittle{
        color: $accent-color;
        text-align: center;
        letter-spacing: 2.4px;
    margin-top: 8px;
    }

    &__block{
      
        padding-top: 70px;
        padding-bottom: 64px;
        display: flex;
        justify-content: space-between;
        @media (max-width:900px) {
            flex-direction: column;
            text-align: center;
        }
        &__item{
            flex-basis: 32%;
            
            img{
                width: 100%;
                @media (max-width:900px) {
                   width: auto;
                }

            }

            &__title{
                line-height: 1.2;
                font-size: 18px;
                font-weight: bold;
                margin-top: 11px;
                letter-spacing: 0.9px;
                margin-bottom: 0;
            }

            &__subtitle{
                font-size: 15px;
                margin-top: 4px;
                span{
                    color: $accent-color;
                }
            }
            &__text{
                color: #696980;
                @media (max-width:900px) {
                    max-width: 500px;
                    margin: 0 auto;
                    margin-bottom: 50px;
                }
            }
        }
    }
    &__button{
        margin: 0 auto ;
        text-align: center;
        display: block;
        text-decoration: none;
        max-width: 266px;
        padding: 12px 0;
        color: #fff;
        border-radius: 3px;
        font-size: 18px;
        letter-spacing: 0.7px;
        background-image: linear-gradient(to left, #65e49c, #64e4a1, #64e4a6, #64e4ab, #65e4b0);
    
        @media (max-width:1280px) {
           margin: 0 auto;
         }
    
    }
}
.page-news{
   
    &__block{
        padding-top: 157px;
        padding-bottom: 138px;
        display: flex;
        justify-content: space-between;
        @media (max-width:1220px) {
            flex-direction: column;
            text-align: center;
        }
        &__text{
            flex-basis: 49%;
            @media (max-width:1280px) {
                max-width: 700px;
                margin: 0 auto;
                margin-bottom: 40px;
            }

            h2{
                font-size: 48px;
                font-weight: bold;
                letter-spacing: 2px;
                line-height: 1;
            }
            p{
                color: #696980;
                margin-top: 11px;
            }
        }

        &__form{
    flex-basis: 49%;
            input{
              
                width: 100%;
                box-sizing: border-box;
               
                background: $black;
                color: #9191b5;
                outline: none;
                border: 1px solid $black;
                padding:14px 28px;
                border-radius: 3px;
              @media (max-width:1280px) {
                    width: 63%;
                    margin-bottom: 21px;
            }
        }
            button{
                height: 49px;
                letter-spacing: 2.1px;
                width: 100%;
                display: block;
                color: #fff;
                background: $accent-color;
                border-radius: 3px ;
                margin-top: 24px;
                outline: none;
                font-size: 18px;
                border:1px solid $accent-color;
                cursor: pointer;
              
                @media (max-width:1280px) {
                    width: 63%;
    display: inline-block;
               
                        }
                }
            }
        }
}

.footer{
    background: $black;
    padding-top: 159px;
    padding-bottom: 129px;
        .page-footer{
            &__block{
            
                display: flex;
                justify-content: space-between;
                @media (max-width:960px) {
                    flex-direction: column;
                   
                 }
                
              
                &__item{
                    @media (max-width:960px) {
                        margin: 0 auto;
                       
                     }


                    &__one{
                        flex-basis: 34%;
                    }
                    &__two{
                        flex-basis: 26%;
                        h4{
                            margin-top: 15px;
                            letter-spacing: 1.2px;
                        }
                        a{
                            margin-top: 9px;
                        }
                    }
                    &__three{
                        flex-basis: 22%;
                        h4{
                            margin-top: 16px;
                        }
                    }

                    &__social{
                        list-style-type: none;
                        padding-left: 0;
                        li{
                            display:inline-block;
                            padding-right: 8px;
                            a{
                                display: block;
                                color: $accent-color;
                            }
                        }
                    }

                    h4{
                        font-size: 18px;
                        color: $accent-color;
                        font-weight: bold;
                        text-transform: uppercase;
                    }
                    a{
                        text-decoration: none;
                        display: block;
                        color:#9191b5;
                    }
                    p{
                        margin-top: 31px;
                        color:#9191b5;
                    }
                    &__logo{
                        display: block;
                        background: url("../img/footer-logo.png") no-repeat;
                        width: 138px;
                        height: 28px;
                        
                    }
                }
            }
    }
}


.page-reviews{
    background: $black;
    padding: 219px 0;
    &__coursel{
        text-align: center;
       &__item{

        &__kav{
            position: relative;
            display: block;
            width: 152px;
            height: 152px;
            margin: 0 auto;
            margin-bottom: 26px;
            
        &__kavz{
            position: absolute;
            display: block;
            background: url("../img/kav.png") no-repeat ;
            width: 48px;
            height: 42px;
            left: 117px;
            top: -1px;
        }
        &__thumb{
            display: block;
            margin: 0 auto;
            position: relative;
            overflow: hidden;
            width: 152px;
            height: 152px;
            border-radius: 100%;
            margin-bottom: 26px;
           
            img{
                width: 152px;
                height: 152px;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                object-fit:cover;
            }
        }
    }
            h3{
                font-size: 24px;
                color: $accent-color;
                font-weight: bold;

            }
            &__subtitle{
                margin-top: 5px;
                margin-bottom: 34px;
            }
            &__text{
                max-width: 745px;
                margin: 0 auto;
                margin-bottom: 37px;
                line-height: 1.9;
            }

            p{
                color: #9191b5;
            }
    }
}
}
